/* sub header CSS */
.sub-header {
  width: 100%;
  height: fit-content;
  position: relative;



  .gnb-wrap {
    width: 100%;
    padding: 1.3rem 0;
    border-bottom: 1px solid #d7d7d7;
    z-index: $nav-z-index;

    .gnb-inner-wrap {
      width: 100%;
      display: flex;
      justify-content: space-between;
      max-width: 1800px;
      min-width: 1280px;
      margin: 0 auto;
      .ci {
        order: 1;
        width: 100px;
        height: 48px;
        box-sizing: border-box;
        margin: 0 3rem;
        flex-basis: 160px;
        a {
          display: block;
          width: fit-content;
          height: fit-content;
          img {
            width: 100px;
            height: 48px;
            object-fit: contain;
          }
        }

      }
      .change-lang {
        order: 3;
        width: fit-content;
        flex-basis: 160px;
        margin: 0 3rem;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        //max-width: 25px;
        overflow: hidden;
        .btn-lang {
          order: 2;
          display: block;
          width: 25px;
          height: 25px;
          margin-left: 0.8rem;
          background-image: url("../images/ico_lang.png");
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center;
          flex-basis: 25px;
        }
        .lang-list-wrap {
          width: fit-content;
          height: 2rem;
          max-width: 0;
          overflow: hidden;
          transition: max-width 0.3s;
          &.active {
            max-width: 75px;
          }
          .lang-list {
            order: 1;
            display: flex;
            align-items: center;
            text-align: center;
            flex-basis: 0;
            .list-item {
              transition: 0.3s;
              &.active {
                a {
                  color: $black;
                  position: relative;
                  &::after {
                    position: absolute;
                    display: block;
                    content: '';
                    width: 2rem;
                    height: 2px;
                    background-color: $black;
                    bottom: -3px;
                  }
                }
              }
              a {
                display: block;
                width: fit-content;
                box-sizing: border-box;
                padding: 0 0.8rem;
                color: #a9aaa9;
                transition: 0.3s;
              }

            }
          }
        }
      }

      .nav-list {
        order: 2;
        width: 100%;
        height: 5rem;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        max-width: 1280px;

        .list-item {
          flex-basis: 18.2rem;

          .tit-l {
            text-align: center;
            font-weight: $b;
            @include bdrs(1.4rem);
            transition: background-color 0.3s ease;
            .nav-item {
              display: block;
              width: 100%;
              height: 100%;
              padding: 1.7rem 3.2rem;
              box-sizing: border-box;
            }
            &:hover {
              background-color: $blue_l;

              a {
                color: $white;
              }
            }
          }
        }
      }
    }
  }

  .d2-list-wrap {
    position: absolute;
    top: 7.7rem;
    left: 0;
    right: 0;
    background-color: #ffffff;
    height: fit-content;
    //max-height: 350px;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.5s ease;
    z-index: $nav-z-index;

    .d2-list {
      width: 1280px;
      height: 332px;
      margin: 0 auto;
      padding: 1.3rem 1.4rem;
      box-sizing: border-box;
      display: flex;
      gap: 9.4rem;

    }

    .d2-item {
      width: 22rem;
      @include flex-col(0.5rem);

      .tit-box {
        @include flex-col(0.5rem);
        justify-content: flex-end;
        padding: 1.2rem 1.8rem;
        border-radius: 1rem;
        height: 5.2rem;
        //height: fit-content;
        box-sizing: border-box;

        a {
          display: block;
          width: 100%;
          height: 100%;
          box-sizing: border-box;
        }
      }

      .img-box {
        width: 23rem;
        height: 74px;
        margin: 0 1.8rem;
        border-radius: 10px;
        box-sizing: border-box;
        overflow: hidden;
        border: 1px solid #e1e1e1;

        a {
          display: block;
          width: 100%;
          height: 100%;

          img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition: transform 0.3s;
            &.ty02 {
              width: 33px;
              height: 50px;
            }
            &.ty03 {
              width: 47px;
              height: 46px;

            }
          }
        }
        &.ty02 {
          a {
            background-color: #f4f3f3;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
              object-fit: contain;
            }
          }
        }
      }

      &:hover {
        .img-box {
          border: 2px solid $blue_l;

          a {
            img {
              transform: scale(1.1);
            }
          }
        }

      }
    }

    .d3-list {
      width: 100%;
      @include flex-col();

      .d3-list-item {
        a {
          display: block;
          padding: 1.2rem 1.8rem;
          border-radius: 1rem;
          transition: background-color 0.3s ease;

          &:hover {
            color: $blue_l;
            font-weight: $b;
            background-color: $light_blue;
          }
        }
      }
    }

    &.active {
      max-height: 350px;
      border-bottom: 3px solid $blue_l;
    }
  }

  .d2-list-wrap.v1 {
    display: flex;
    justify-content: center;

    .d2-section {
      padding-top: 3rem;
      box-sizing: border-box;
      @include flex-col();
      &.d2-sec-1 {
        width: 620px;
        .d2-section-tit {
          margin: 0 1.4rem 0 3.2rem;
        }
        .d2-list {
          width: 620px;
        }
      }
      &.d2-sec-2 {
        width: 660px;
        .d2-section-tit {
          margin: 0 3.2rem 0 1.8rem;
        }
        .d2-list {
          width: 660px;
        }
      }
      .d2-section-tit {
        border: 1px solid #e1e1e1;
        border-radius: 0.5rem;
        padding: 0.5rem 0;
        display: flex;
        align-items: center;
        justify-content: center;
        .tit-m {
          font-weight: $r;
          color: #555555;
        }

      }
    }
    .d2-list {
      width: 640px;
    }
    .d2-item {
      .tit-box {
        height: auto;
      }
    }
  }

  .d2-list-wrap.v2 {
    display: flex;
    justify-content: center;

    .d2-section {
      //width: 640px;
      padding-top: 3rem;
      box-sizing: border-box;
      @include flex-col();
      &.d2-sec-1 {
        width: 500px;
        .d2-list {
          width: 500px;
        }
      }
      &.d2-sec-2 {
        width: 750px;
        margin-right: 3rem;
        .d2-list {
          width: 750px;
        }
      }
      .d2-section-tit {
        margin-left: 3rem;
        border: 1px solid #e1e1e1;
        border-radius: 0.5rem;
        padding: 0.5rem 0;
        display: flex;
        align-items: center;
        justify-content: center;
        .tit-m {
          font-weight: $r;
          color: #555555;
        }
      }
    }
    .d2-list {
      width: 100%;
      justify-content: space-between;
      flex-wrap: wrap;
      row-gap: 4rem;
      column-gap: 1rem;
      align-content: flex-start;

      .d2-list-item {
        width: fit-content;
        height: fit-content;
      }
    }
    .d2-item {
      width: 23rem;
      gap: 0;
      .tit-box {
        height: auto;
        padding: 0.9rem 1.8rem;
      }
    }
  }
  .d2-list-wrap.v3,
  .d2-list-wrap.v4 {

    .d2-list {
      height: 104px;
      justify-content: space-between;
      gap: 0;
      padding-top: 3.2rem;
    }

    .d2-item {
      .tit-box {
        height: fit-content;
        justify-content: center;
        text-align: center;
        transition: background-color 0.3s ease;
        padding: unset;
        a {
          display: block;
          box-sizing: border-box;
          width: 100%;
          height: 100%;
          padding: 1.2rem 1.8rem;
        }
      }

      &:hover {
        .tit-box {
          background-color: $light_blue;
          color: $blue_l;
        }
      }
    }
  }
  .d2-list-wrap.v3 {
    .d2-item {
      width: 30rem;
    }
  }
  .d2-list-wrap.v4 {
    .d2-item {
      width: 21rem;
    }
  }






  &.ty02 {
    .gnb-wrap {
      position: absolute;
      top: 0;
      left: 0;
      transition: background-color 0.3s ease;
      background-color: $white;
      @include bdb(1px, #d7d7d733);
    }
  }
}


/* // sub header CSS */

/* breadcrumb CSS */

.breadcrumb-wrap {
  width: 100%;
  padding: 1.3rem 0;
  border-bottom: 1px solid #d7d7d7;

  .breadcrumb {
    width: 1280px;
    margin: 0 auto;

    .bread-list {
      display: flex;
      justify-content: flex-end;
      gap: 2.5rem;
    }
  }
}

/* // breadcrumb CSS */

select {
  display: block;
  padding: 1rem 4.2rem 1rem 1.6rem;
  border-radius: 3.2rem;
  font-size: 1.15rem;
  //    
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  background-position: calc(100% - 1.5rem) center;
  background-repeat: no-repeat;

  option {
    font-size: 1.15rem;
    padding: 1rem 0;
  }

  &:focus,
  :focus-visible {

    outline-color: $blue_l;
  }

}

.select-ty {
  background-image: url("../images/ico_arrow_003.png");
  background-size: 1.2rem 0.6rem;
  transition: background 0.2s ease;

  &.active {
    background-image: url("../images/ico_arrow_002.png");
  }
}

/* handleScroll Event Class */
.header-fix {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  height: 7.7rem;
  color: $white;
  background-color: $blue_l;
  z-index: $nav-z-index + 1;
  h3 {
    display: block;
    width: $pc-min;
    margin: 0 auto;
  }
}
.header-fix-div {
  transition: all 0.3s;
}
.header-fix-padding {
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 4.2rem;
  }
}
/* //handleScroll Event Class */